<template>
    <div class="layout">
        <Layout>
            <Header>
                <Menu mode="horizontal" theme="dark" @on-select="logout">
                    <!-- <div class="layout-logo"></div> -->
                    <div class="layout-nav">
                        <MenuItem name="phone">
                            <Icon type="ios-contact" />
                            {{ $config.appInfo.user.email }}
                        </MenuItem>
                    </div>
                </Menu>
            </Header>
            <Layout :style="{padding: '0 50px'}">
                <Breadcrumb :style="{margin: '16px 0'}">
                    <BreadcrumbItem>Makani</BreadcrumbItem>
                    <BreadcrumbItem>{{ $route.name }}</BreadcrumbItem>
                </Breadcrumb>
                <Content :style="{padding: '24px 0', minHeight: '280px', background: '#fff'}">
                    <Layout>
                        <Sider hide-trigger :style="{background: '#fff', display: $config.loginStatus ? 'block' : 'none'}">
                            <Menu :active-name="getActiveMenu()" theme="light" width="auto" :open-names="['management']" @on-select="changeRoute">
                                <MenuItem name="home">
                                    <Icon type="ios-home"></Icon>
                                    Home
                                </MenuItem>
                                <Submenu name="management">
                                    <template slot="title">
                                        <Icon type="ios-build" />
                                        Management
                                    </template>
                                    <MenuItem name="foods">Foods</MenuItem>
                                    <MenuItem name="orders">Orders</MenuItem>
                                    <MenuItem name="comments">Comments</MenuItem>
                                </Submenu>
                                <MenuItem name="admin" v-if="$config.appInfo.user.admin">
                                    <Icon type="ios-hammer" />
                                    Admin
                                </MenuItem>
                            </Menu>
                        </Sider>
                        <Content :style="{padding: '24px', minHeight: '280px', background: '#fff'}">
                            <router-view />
                        </Content>
                    </Layout>
                </Content>
            </Layout>
            <Footer class="layout-footer-center">2018 &copy; Makani</Footer>
        </Layout>
    </div>
</div>
</template>
<script>
export default {
    name: 'App',
    mounted: function () {
        this.$config.getInfo()
    },
    data: function () {
        return {

        }
    },
    methods: {
        getActiveMenu: function () {
            return this.$lodash.toLower(this.$route.name)
        },
        changeRoute: function (name) {
            this.$router.push({name: this.$lodash.upperFirst(name)})
        },
        logout: async function () {
            await this.$axios.get('/logout')
            window.location.href = '/'
        }
    }
}
</script>
<style scoped>
.layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
    height: 100vh;
}
.layout-logo{
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
}
.layout-nav{
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
    display: flex;
    flex-direction: row-reverse;
}
.layout-footer-center{
    text-align: center;
}
.ivu-layout-content {
    height: calc(100vh - 187px);
}
.ivu-layout-sider {
    height: calc(100vh - 211px);
}
</style>



