<template>
    <Layout class="home-box">
        <Card :style="{width: '80%', margin: '0 auto'}">
            <p slot="title">
                {{ foodDetail.name }}
            </p>
            <a href="#" slot="extra" @click.prevent="deleteFood" :style="{marginLeft: '10px'}">
                <Icon type="md-trash" />
                Delete
            </a>
            <a href="#" slot="extra" @click.prevent="saveFood" :style="{marginLeft: '10px'}" v-if="editStatus">
                <Icon type="md-cloud-upload"></Icon>
                Save
            </a>
            <a href="#" slot="extra" @click.prevent="editSwitch" :style="{marginLeft: '10px'}" v-if="!editStatus">
                <Icon type="md-create"></Icon>
                Edit
            </a>
            <a href="#" slot="extra" @click.prevent="$router.push({name: 'Foods'})" :style="{marginLeft: '10px'}">
                <Icon type="md-arrow-back" />
                Back
            </a>
            <ul class="food-list">
                    <li>
                        <label>Ingredients</label>
                        <p v-if="!editStatus">{{ foodDetail.ingredients }}</p>
                        <input type="text" v-model="foodDetail.ingredients" v-if="editStatus">
                    </li>
                    <li>
                        <label>Description</label>
                        <p v-if="!editStatus">{{ foodDetail.description }}</p>
                        <input type="text" v-model="foodDetail.description" v-if="editStatus">
                    </li>
                    <li>
                        <label>Price</label>
                        <p v-if="!editStatus">{{ foodDetail.price }}</p>
                        <input type="text" v-model="foodDetail.price" v-if="editStatus">
                    </li>
                    <li>
                        <label>Discount</label>
                        <p v-if="!editStatus">{{ foodDetail.discount }}</p>
                        <input type="text" v-model="foodDetail.discount" v-if="editStatus">
                    </li>
                    <li>
                        <label>Picture</label>
                        <div class="food-pic" v-if="!editStatus">
                            <img :src="foodDetail.pictures[0] ? foodDetail.pictures[0] : require('../assets/no-pic.svg')" v-if="foodDetail.pictures">
                        </div>
                        <Upload
                            ref="foodUpload"
                            accept="image/png, image/jpeg"
                            :format="['jpg','jpeg','png']"
                            :max-size="2048"
                            action="//makani.oss-me-east-1.aliyuncs.com"
                            :before-upload="handleUpload"
                            :on-success="handleUploadSuccess"
                            :data="foodPicFormData"
                            style="float: right;"
                            v-if="editStatus">
                            <Button icon="ios-cloud-upload-outline">Upload images</Button>
                        </Upload>
                    </li>
            </ul>
        </Card>
    </Layout>
</template>
<script>
export default {
    name: 'FoodDetail',
    data: function () {
        return {
            foodDetail: null,
            editStatus: 0,
            foodPicFormData: {}
        }
    },
    created: function () {
        const index = this.$lodash.findIndex(this.$config.foodList, ['_id', this.$route.params.id])
        this.foodDetail = this.$config.foodList[index]
    },
    methods: {
        editSwitch: function () {
            this.editStatus = 1
        },
        saveFood: async function () {
            this.foodDetail.id = this.foodDetail._id
            this.foodDetail.pictures = this.foodDetail.pictures.join(',')
            const result = await this.$axios.post('/edit_food', this.$qs.stringify(this.foodDetail))
            if (result.data.flag === 'success') {
                this.$Message.success('Success')
                this.$router.push({name: 'Foods'})
            }
        },
        deleteFood: async function () {
            const body = {
                id: this.foodDetail._id
            }
            const result = await this.$axios.post('/remove_food', this.$qs.stringify(body))
            if (result.data.flag === 'success') {
                this.$Message.success('Success')
                this.$router.push({name: 'Foods'})
            }
        },
        handleUpload: async function (file) {
            let mime
            switch (file.type) {
                case 'image/png':
                    mime = 'png'
                    break
                case 'image/jpeg':
                    mime = 'jpg'
                    break
                default:
                    return false
            }
            const {data} = await this.$axios.post('/get_pic_token', this.$qs.stringify({
                type: 3,
                mime: mime
            }))
            this.foodPicFormData.OSSAccessKeyId = data.accessid
            this.foodPicFormData.policy = data.policy
            this.foodPicFormData.Signature = data.signature
            this.foodPicFormData.key = data.key
        },
        handleUploadSuccess: function () {
            this.foodDetail.pictures = ['https://makani.oss-me-east-1.aliyuncs.com/' + this.foodPicFormData.key]
        }
    }
}
</script>
<style scoped>
    .home-box {
        background-color: #fff;
    }
    .food-list {
        list-style: none;
        padding-bottom: 20px;
    }
    .food-list li {
        margin-bottom: 20px;
    }
    .food-list p {
        float: right;
    }
    .food-list input {
        float: right;
        max-width: 300px;
        text-align: right;
        margin-top: -2px;
    }
    .food-pic {
        display: inline-block;
        width: 60px;
        height: 60px;
        float: right;
        line-height: 60px;
        border: 1px solid transparent;
        border-radius: 4px;
        box-shadow: 0 1px 1px rgba(0,0,0,.2);
    }
    .food-pic img {
        width: 100%;
        height: 100%;
    }
</style>



