<template>
    <Card :style="{width: '450px', margin: '0 auto'}">
        <Form ref="formCustom" :label-width="105">
            <FormItem label="Email">
                <Input type="text" v-model="email" :readonly="emailReadonly"></Input>
            </FormItem>
            <FormItem label="Password" v-if="loginStep === 0 || loginStep === 1">
                <Input type="password" v-model="password"></Input>
            </FormItem>
            <FormItem label="Password Again" v-if="loginStep === 1">
                <Input type="password" v-model="password2"></Input>
            </FormItem>
            <FormItem label="Name" v-if="loginStep === 1">
                <Input type="text" v-model="nickname"></Input>
            </FormItem>
            <FormItem label="Invitation code" v-if="loginStep === 1">
                <Input type="text" v-model="code"></Input>
            </FormItem>
            <FormItem label="Verification code" v-if="loginStep === 2">
                <Input type="text" v-model="key"></Input>
            </FormItem>
            <FormItem v-if="loginStep === 0">
                <Button type="primary" @click="login">Login</Button>
                <Button style="float: right" @click="switchReg">No account?</Button>
            </FormItem>
            <FormItem v-if="loginStep === 1">
                <Button type="primary" @click="register">Register</Button>
                <Button style="float: right" @click="switchLogin">Have an account?</Button>
            </FormItem>
            <FormItem v-if="loginStep === 2">
                <Button type="primary" @click="verify">Submit</Button>
                <Button style="margin-left: 8px;float: right" @click="reset">Reset</Button>
                <Button style="float: right" type="primary" @click="send">Send code</Button>
            </FormItem>
        </Form>
    </Card>
</template>
<script>
export default {
    name: 'Auth',
    data: function () {
        return {
            loginStep: 0,
            email: null,
            password: null,
            password2: null,
            nickname: null,
            code: null,
            key: null,
            emailReadonly: false
        }
    },
    mounted: function () {

    },
    methods: {
        reset: function () {
            this.loginStep = 0
            this.email = null
            this.password = null
            this.password2 = null
            this.nickname = null
            this.code = null
            this.key = null
            this.emailReadonly = false
        },
        switchReg: function () {
            this.loginStep = 1
        },
        switchLogin: function () {
            this.loginStep = 0
        },
        login: async function () {
            if (!this.email || !this.password) return
            const result = await this.$axios.post('login', this.$qs.stringify({
                email: this.email,
                password: this.password
            }))
            if (result.status === 200 && result.data.flag === 'success') {
                this.$config.getInfo()
            } else if (result.data.flag === 4) {
                this.$Notice.info({
                    title: 'User not activated'
                })
                this.emailReadonly = true
                this.loginStep = 2
            } else {
                this.$Notice.error({
                    title: 'Username or password is wrong'
                })
            }
        },
        register: async function () {
            if (!this.email || !this.password || !this.nickname || !this.code) return
            if (this.password !== this.password2) {
                this.$Notice.info({
                    title: 'Inconsistent password'
                })
                return
            }
            const result = await this.$axios.post('register', this.$qs.stringify({
                email: this.email,
                password: this.password,
                name: this.nickname,
                code: this.code
            }))
            if (result.status === 200 && result.data.flag === 'success') {
                this.emailReadonly = true
                // this.key = result.data.key
                this.loginStep = 2
            } else if (result.status === 200 && result.data.flag === 17) {
                this.$Notice.error({
                    title: 'Invitation code error'
                })
            } else if (result.status === 200 && result.data.flag === 15) {
                this.$Notice.error({
                    title: 'Email already used'
                })
            }
        },
        send: async function () {
            if (!this.email) return
            const result = await this.$axios.get('change_request', {
                params: {
                    email: this.email
                }
            })
            if (result.status === 200 && result.data.flag === 'success') {
                // this.key = result.data.key
            } else if (result.data.flag === 2) {
                this.$Notice.error({
                    title: `Wait ${result.data.time} seconds to send`
                })
            }
        },
        verify: async function () {
            if (!this.email || !this.key) return
            const result = await this.$axios.get('verify', {
                params: {
                    email: this.email,
                    key: this.key
                }
            })
            if (result.status === 200 && result.data.flag === 'success') {
                this.emailReadonly = false
                this.key = null
                this.loginStep = 0
            } else if (result.status === 200 && result.data.flag === 6) {
                this.$Notice.error({
                    title: 'Verification code error'
                })
            } else if (result.status === 200 && result.data.flag === 16) {
                this.$Notice.error({
                    title: 'User already activated'
                })
            }
        }
    }
}
</script>
<style scoped>
.auth-card {
    margin: 0 auto;
}
</style>



