<template>
    <Layout class="home-box">
        <Card :style="{width: '80%', margin: '0 auto'}">
            <a href="#" slot="extra" @click.prevent="$router.push({name: 'ShopEdit'})">
                <Icon type="md-create"></Icon>
                Edit
            </a>
            <div style="text-align:center">
                <img :src="$config.appInfo.shop.profile + ($config.appInfo.shop.profile.indexOf('gravatar') === -1 ? '?x-oss-process=style/sys_profile' : '')" v-if="$config.appInfo.shop.profile">
                <h3>{{ $config.appInfo.shop.shop_name }}</h3>
                <p>{{ $config.appInfo.shop.introduction }}</p>
            </div>
            <Form :label-width="150">
                <FormItem label="Business status">
                    <i-switch :value="active" size="large" :true-value="1" :false-value="0" :loading="active_loading" @on-change="active_change">
                        <span slot="open">ON</span>
                        <span slot="close">OFF</span>
                    </i-switch>
                </FormItem>
            </Form>
        </Card>
    </Layout>
</template>
<script>
export default {
    name: 'Home',
    data: function () {
        return {
            active: undefined
        }
    },
    computed: {
        active_loading: function () {
            return this.active === undefined
        }
    },
    mounted: function () {
        if (this.$config.appInfo.shop.active === undefined) {
            this.$config.getInfo(() => {
                this.active = this.$config.appInfo.shop.active
            })
        } else {
            this.active = this.$config.appInfo.shop.active
        }
    },
    methods: {
        active_change: async function (val) {
            const body = {
                active: val
            }
            const result = await this.$axios.post('/business', this.$qs.stringify(body))
            if (result.data.flag === 'success') {
                this.$Message.success('Success')
            }
        }
    }
}
</script>
<style scoped>
    .home-box {
        background-color: #fff;
    }
</style>



