<template>
    <Layout class="home-box">
        <Card :style="{width: '450px', margin: '0 auto'}">
            <p slot="title">Edit shop infomation</p>
            <a href="#" slot="extra" @click.prevent="saveShopInfo">
                <Icon type="md-cloud-upload" />
                Save
            </a>
            <a href="#" slot="extra" @click.prevent="handleBack" :style="{marginLeft: '10px'}">
                <Icon type="md-arrow-back" />
                Back
            </a>
            <Form :model="shopInfo" :label-width="75">
                <FormItem label="Name">
                    <Input v-model="shopInfo.name" type="text"></Input>
                </FormItem>
                <FormItem label="Intro">
                    <Input v-model="shopInfo.intro" type="text"></Input>
                </FormItem>
                <FormItem label="Profile">
                    <Input v-model="shopInfo.profile" type="text"></Input>
                    <Upload
                        accept="image/png, image/jpeg"
                        :format="['jpg','jpeg','png']"
                        :max-size="2048"
                        action="//makani.oss-me-east-1.aliyuncs.com"
                        :before-upload="handleProfileUpload"
                        :on-success="handleProfileUploadSuccess"
                        :data="shopProfileFormData">
                        <Button icon="ios-cloud-upload-outline">Upload image</Button>
                    </Upload>
                </FormItem>
                <FormItem label="Pictures">
                    <div class="shoppic-upload-list" v-for="(pic, index) in shopPicList" :key="index">
                        <template v-if="pic.status === 'finished'">
                            <img :src="pic.url">
                            <div class="shoppic-upload-list-cover">
                                <Icon type="ios-eye-outline" @click.native="handlePicView(pic.url)"></Icon>
                                <Icon type="ios-trash-outline" @click.native="handlePicRemove(pic.url)"></Icon>
                            </div>
                        </template>
                        <template v-else>
                            <Progress v-if="pic.showProgress" :percent="picPercentage" hide-info status="active"></Progress>
                        </template>
                    </div>
                    <Upload
                        ref="picUploader"
                        :show-upload-list="false"
                        accept="image/png, image/jpeg"
                        :format="['jpg','jpeg','png']"
                        :max-size="2048"
                        action="//makani.oss-me-east-1.aliyuncs.com"
                        :before-upload="handlePicUpload"
                        :on-progress="handlePicUploading"
                        :on-success="handlePicUploadSuccess"
                        :data="picFormData">
                        <Button icon="ios-cloud-upload-outline">Upload image</Button>
                    </Upload>
                </FormItem>
                <FormItem label="Address">
                    <Input v-model="shopInfo.address" type="text"></Input>
                </FormItem>
                <FormItem label="Contact">
                    <Input v-model="shopInfo.contact" type="text"></Input>
                </FormItem>
                <FormItem label="Location" >
                    <Row>
                        <Col span="11">
                            <Input v-model="shopInfo.X" type="text" placeholder="latitude"></Input>
                        </Col>
                        <Col span="2" style="text-align: center">&</Col>
                        <Col span="11">
                            <Input v-model="shopInfo.Y" type="text" placeholder="longitude"></Input>
                        </Col>
                    </Row>
                    <Button icon="md-locate" @click="handleMapJump">Show Map</Button>
                </FormItem>
                <FormItem label="Tags">
                    <Input v-model="shopInfo.tags" type="text"></Input>
                </FormItem>
                <FormItem label="Deliverable">
                    <i-switch v-model="shopInfo.deliverable" size="large">
                        <span slot="open">True</span>
                        <span slot="close">False</span>
                    </i-switch>
                </FormItem>
            </Form>
        </Card>
        <Modal title="View Image" v-model="viewerVisible" ok-text="OK" cancel-text="Cancel">
            <img :src="viewerSrc" v-if="viewerVisible" style="width: 100%">
        </Modal>
    </Layout>
</template>
<script>
export default {
    name: 'ShopEdit',
    data: function () {
        return {
            shopInfo: {},
            shopProfileFormData: {},
            shopPicList: [],
            picFormData: {},
            viewerVisible: false,
            viewerSrc: 'https://source.unsplash.com/random',
            picPercentage: 0
        }
    },
    computed: {
        picString: function () {
            let urlArr = []

            this.shopPicList.forEach((pic) => {
                urlArr.push(pic.url)
            })

            return urlArr.join(',')
        }
    },
    created: function () {
        if (this.$config.shopCache) {
            this.shopInfo = this.$config.shopCache.shopInfo
            this.shopPicList = this.$config.shopCache.shopPicList
        } else {
            this.switchInfo()
        }
    },
    methods: {
        switchInfo: async function () {
            this.shopInfo = {
                name: this.$config.appInfo.shop.shop_name,
                intro: this.$config.appInfo.shop.introduction,
                profile: this.$config.appInfo.shop.profile,
                address: this.$config.appInfo.shop.address,
                contact: this.$config.appInfo.shop.contact,
                deliverable: this.$config.appInfo.shop.deliverable
            }
            if (this.$config.appInfo.shop.tags.length > 0) {
                this.shopInfo.tags = this.$config.appInfo.shop.tags.join()
            } else {
                this.shopInfo.tags = ''
            }
            if (this.$config.appInfo.shop.coordinates.length > 0) {
                this.shopInfo.X = this.$config.appInfo.shop.coordinates[0]
                this.shopInfo.Y = this.$config.appInfo.shop.coordinates[1]
            } else {
                this.shopInfo.X = ''
                this.shopInfo.Y = ''
            }
            let _picArr = this.$lodash.compact(this.$config.appInfo.shop.pictures)
            if (_picArr.length > 0) {
                _picArr.forEach((url) => {
                    this.shopPicList.push({
                        url: url,
                        status: 'finished'
                    })
                })
            }
            this.shopInfo.pictures = this.picString
        },
        saveShopInfo: async function () {
            const result = await this.$axios.post('/edit_info', this.$qs.stringify(this.shopInfo))
            if (result.data.flag === 'success') {
                await this.$config.getInfo()
                this.$Message.success('Success')
                this.$config.shopCache = undefined
            } else if (result.data.flag === 1) {
                this.$Message.error('Location is required & must be float')
            }
        },
        getMime: function (type) {
            let mime
            switch (type) {
                case 'image/png':
                    mime = 'png'
                    break
                case 'image/jpeg':
                    mime = 'jpg'
                    break
                default:
                    return false
            }
            return mime
        },
        handleProfileUpload: async function (file) {
            let _mime = this.getMime(file.type)
            if (!_mime) return

            const {data} = await this.$axios.post('/get_pic_token', this.$qs.stringify({
                type: 1,
                mime: _mime
            }))
            this.shopProfileFormData.OSSAccessKeyId = data.accessid
            this.shopProfileFormData.policy = data.policy
            this.shopProfileFormData.Signature = data.signature
            this.shopProfileFormData.key = data.key
        },
        handleProfileUploadSuccess: function () {
            this.shopInfo.profile = 'https://makani.oss-me-east-1.aliyuncs.com/' + this.shopProfileFormData.key
        },
        handlePicUpload: async function (file) {
            let _mime = this.getMime(file.type)
            if (!_mime) return

            const {data} = await this.$axios.post('/get_pic_token', this.$qs.stringify({
                type: 2,
                mime: _mime
            }))
            this.picFormData.OSSAccessKeyId = data.accessid
            this.picFormData.policy = data.policy
            this.picFormData.Signature = data.signature
            this.picFormData.key = data.key

            this.shopPicList.push({
                url: 'https://makani.oss-me-east-1.aliyuncs.com/' + data.key,
                status: 'progress',
                showProgress: true
            })
        },
        handlePicUploading: function (event) {
            this.picPercentage = event.percent
        },
        handlePicUploadSuccess: function () {
            this.shopPicList[this.shopPicList.length - 1].status = 'finished'
            this.shopInfo.pictures = this.picString
        },
        handlePicView: function (url) {
            this.viewerVisible = true
            this.viewerSrc = url
        },
        handlePicRemove: function (url) {
            const index = this.$lodash.findIndex(this.shopPicList, ['url', url])
            this.shopPicList.splice(index, 1)
            this.shopInfo.pictures = this.picString
        },
        handleMapJump: function () {
            this.$config.shopCache.shopInfo = this.shopInfo
            this.$config.shopCache.shopPicList = this.shopPicList
            this.$router.push({name: 'Map'})
        },
        handleBack: function () {
            this.$config.shopCache = undefined
            this.$router.push({name: 'Home'})
        }
    }
}
</script>
<style scoped>
    .home-box {
        background-color: #fff;
    }
    .shoppic-upload-list{
        display: inline-block;
        width: 60px;
        height: 60px;
        text-align: center;
        line-height: 60px;
        border: 1px solid transparent;
        border-radius: 4px;
        overflow: hidden;
        background: #fff;
        position: relative;
        box-shadow: 0 1px 1px rgba(0,0,0,.2);
        margin-right: 4px;
    }
    .shoppic-upload-list img{
        width: 100%;
        height: 100%;
    }
    .shoppic-upload-list-cover{
        display: none;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0,0,0,.6);
    }
    .shoppic-upload-list:hover .shoppic-upload-list-cover{
        display: block;
    }
    .shoppic-upload-list-cover i{
        color: #fff;
        font-size: 20px;
        cursor: pointer;
        margin: 0 2px;
    }
</style>



