<template>
    <Layout class="home-box">
        <ButtonGroup :style="{marginBottom: '10px'}">
            <Button type="primary" @click="foodModal = true">
                <Icon type="md-add" />
            </Button>
        </ButtonGroup>
        <Table
            :columns="foodColumns"
            :data="$config.foodList"
            no-data-text="No data"
            :loading="foodLoading"
            @on-row-click="clickFood"
            >
        </Table>
        <Modal
            v-model="foodModal"
            title="Add food"
            ok-text="submit"
            cancel-text="cancel"
            @on-ok="submitFood">
            <Form :model="formItem" :label-width="80">
                <FormItem label="Name">
                    <Input v-model="formItem.name"></Input>
                </FormItem>
                <FormItem label="Ingredients">
                    <Input v-model="formItem.ingredients"></Input>
                </FormItem>
                <FormItem label="Description">
                    <Input v-model="formItem.description"></Input>
                </FormItem>
                <FormItem label="Price">
                    <Input v-model="formItem.price"></Input>
                </FormItem>
                <FormItem label="Discount">
                    <Input v-model="formItem.discount"></Input>
                </FormItem>
                <FormItem label="Picture">
                    <Upload
                        ref="foodUpload"
                        accept="image/png, image/jpeg"
                        :format="['jpg','jpeg','png']"
                        :max-size="2048"
                        action="//makani.oss-me-east-1.aliyuncs.com"
                        :before-upload="handleUpload"
                        :on-success="handleUploadSuccess"
                        :data="foodPicFormData">
                        <Button icon="ios-cloud-upload-outline">Upload images</Button>
                    </Upload>
                </FormItem>
            </Form>
        </Modal>
    </Layout>
</template>
<script>
export default {
    name: 'Foods',
    data: function () {
        return {
            foodModal: false,
            foodLoading: true,
            formItem: {
                name: '',
                pictures: '',
                ingredients: '',
                description: '',
                price: 0,
                discount: 0
            },
            foodColumns: [
                {
                    title: 'Name',
                    key: 'name',
                    maxWidth: 200,
                    tooltip: true
                },
                {
                    title: 'Description',
                    key: 'description',
                    tooltip: true
                },
                {
                    title: 'Price',
                    key: 'price',
                    width: 100
                },
                {
                    title: 'Discount',
                    key: 'discount',
                    width: 100
                }
            ],
            foodPicFormData: {

            }
        }
    },
    mounted: function () {
        this.getFoods()
    },
    methods: {
        getFoods: async function () {
            const list = await this.$axios.get('/menu')
            this.$config.foodList = list.data
            this.foodLoading = false
        },
        submitFood: async function () {
            const result = await this.$axios.post('/add_food', this.$qs.stringify(this.formItem))
            if (result.data.flag === 'success') {
                this.formItem._id = result.data.id
                this.$config.foodList.push(this.formItem)
                this.$refs.foodUpload.clearFiles()
                this.clearFood()
            }
        },
        clearFood: function () {
            this.formItem = {
                name: '',
                pictures: '',
                ingredients: '',
                description: '',
                price: 0,
                discount: 0
            }
        },
        clickFood: function (data, index) {
            this.$router.push('/food/' + data._id)
        },
        handleUpload: async function (file) {
            let mime
            switch (file.type) {
                case 'image/png':
                    mime = 'png'
                    break
                case 'image/jpeg':
                    mime = 'jpg'
                    break
                default:
                    return false
            }
            const {data} = await this.$axios.post('/get_pic_token', this.$qs.stringify({
                type: 3,
                mime: mime
            }))
            this.foodPicFormData.OSSAccessKeyId = data.accessid
            this.foodPicFormData.policy = data.policy
            this.foodPicFormData.Signature = data.signature
            this.foodPicFormData.key = data.key
        },
        handleUploadSuccess: function () {
            this.formItem.pictures = 'https://makani.oss-me-east-1.aliyuncs.com/' + this.foodPicFormData.key
        }
    }
}
</script>
<style scoped>
    .home-box {
        background-color: #fff;
    }
</style>



