<template>
    <Layout class="home-box">
        <Table
            :columns="commentColumns"
            :data="$config.commentList"
            no-data-text="No data"
            :loading="commentLoading"
            @on-row-click="clickComment"
            >
        </Table>
        <Modal
            v-model="commentModal"
            title="Comment details"
            footer-hide
            draggable
            @on-visible-change="modalToggle">
            <CellGroup>
                <Cell title="Time" :extra="commentDetailData.time" />
                <Cell title="Name" :extra="commentDetailData.user" />
                <Collapse value="content" simple>
                    <Panel name="content" hide-arrow>
                        <div class="ivu-cell-title">Comment Detail</div>
                        <p slot="content">{{ commentDetailData.content }}</p>
                    </Panel>
                </Collapse>
                <Collapse value="pic" simple>
                    <Panel name="pic" hide-arrow>
                        <div class="ivu-cell-title">Comment Picture</div>
                        <div slot="content">
                            <div class="comment-pic" v-for="(pic, index) in commentDetailData.pic" :key="index">
                                <img :src="pic + '?x-oss-process=style/sys_comment_thumbnail'">
                                <div class="comment-pic-list-cover">
                                    <Icon type="ios-eye-outline" @click.native="handlePicView(pic)"></Icon>
                                </div>
                            </div>
                        </div>
                    </Panel>
                </Collapse>
            </CellGroup>
        </Modal>
        <Modal title="View Image" v-model="viewerVisible" ok-text="OK" cancel-text="Cancel">
            <img :src="viewerSrc" v-if="viewerVisible" style="width: 100%">
        </Modal>
    </Layout>
</template>
<script>
export default {
    name: 'Comments',
    data: function () {
        return {
            commentModal: false,
            commentLoading: true,
            commentColumns: [
                {
                    title: 'Time',
                    key: 'time',
                    maxWidth: 200,
                    tooltip: true
                },
                {
                    title: 'User',
                    key: 'nickname',
                    maxWidth: 200,
                    tooltip: true
                },
                {
                    title: 'Content Preview',
                    key: 'comment',
                    tooltip: true
                }
            ],
            commentDetailData: {
                time: '',
                user: '',
                content: '',
                pic: []
            },
            viewerVisible: false,
            viewerSrc: 'https://source.unsplash.com/random'
        }
    },
    mounted: function () {
        this.getComments()
    },
    methods: {
        getComments: async function () {
            const list = await this.$axios.get('/view_comments', {
                params: {
                    start: 0
                }
            })
            let commentArr = list.data
            commentArr.forEach((val, i) => {
                commentArr[i]['time'] = new Date(val.time * 1000).toLocaleString()
            })
            this.$config.commentList = commentArr
            this.commentLoading = false
        },
        clickComment: function (data, index) {
            this.commentDetailData.time = data.time
            this.commentDetailData.user = data.nickname
            this.commentDetailData.content = data.comment
            this.commentDetailData.pic = data.pictures
            this.commentModal = true
        },
        modalToggle: function (status) {
            if (!status) {
                this.commentDetailData = {
                    time: '',
                    user: '',
                    content: '',
                    pic: []
                }
            }
        },
        handlePicView: function (pic) {
            this.viewerSrc = pic
            this.viewerVisible = true
        }
    }
}
</script>
<style scoped>
    .home-box {
        background-color: #fff;
    }
    .ivu-collapse {
        border: 0 !important;
    }
    .ivu-cell-title {
        display: inline;
    }
    .comment-pic {
        display: inline-block;
        width: 40px;
        height: 40px;
        line-height: 40px;
        border: 1px solid transparent;
        border-radius: 4px;
        box-shadow: 0 1px 1px rgba(0,0,0,.2);
        position: relative;
        overflow: hidden;
        margin-right: 4px;
    }
    .comment-pic img {
        width: 100%;
        height: 100%;
    }
    .comment-pic-list-cover{
        display: none;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0,0,0,.6);
        text-align: center;
    }
    .comment-pic:hover .comment-pic-list-cover{
        display: block;
    }
    .comment-pic-list-cover i{
        color: #fff;
        font-size: 20px;
        cursor: pointer;
        margin: 0 2px;
    }
</style>



