<template>
    <Layout class="home-box">
        <Card :style="{width: '90%', margin: '0 auto'}">
            <p slot="title">Select location</p>
            <a href="#" slot="extra" @click.prevent="confirm">
                <Icon type="md-checkbox-outline" />
                Confirm
            </a>
            <a href="#" slot="extra" @click.prevent="$router.push({name: 'ShopEdit'})" :style="{marginLeft: '10px'}">
                <Icon type="md-arrow-back" />
                Back
            </a>
            <l-map
                style="width: 100%; height: calc(100vh - 370px);"
                :zoom="mapZoom"
                :center="mapCenter"
                @update:center="centerUpdated"
            >
                <l-tile-layer :url="mapUrl"></l-tile-layer>
                <l-marker :lat-lng="mapCenter" ></l-marker>
            </l-map>
        </Card>
    </Layout>
</template>
<script>
import { LMap, LTileLayer, LMarker } from 'vue2-leaflet'

export default {
    name: 'WorldMap',
    components: {
        LMap,
        LTileLayer,
        LMarker
    },
    data: function () {
        return {
            mapZoom: 6,
            mapCenter: [24.723628, 46.672808],
            mapUrl: 'http://server.arcgisonline.com/ArcGIS/rest/services/World_Street_Map/MapServer/tile/{z}/{y}/{x}'
        }
    },
    created: function () {
        this.getCenter()
    },
    methods: {
        getCenter: function () {
            if (this.$config.appInfo) {
                if (this.$config.appInfo.shop.coordinates.length === 2 && this.$config.appInfo.shop.coordinates[0] !== 0) {
                    this.mapCenter = [
                        this.$config.appInfo.shop.coordinates[0],
                        this.$config.appInfo.shop.coordinates[1]
                    ]
                    this.mapZoom = 18
                }
            }
        },
        centerUpdated: function (center) {
            this.mapCenter = center
        },
        confirm: function () {
            this.$config.shopCache.shopInfo.X = this.mapCenter.lat.toFixed(6)
            this.$config.shopCache.shopInfo.Y = this.mapCenter.lng.toFixed(6)
            this.$router.push({name: 'ShopEdit'})
        }
    }
}
</script>
<style scoped>
    .home-box {
        background-color: #fff;
        height: 100%;
    }
    .ivu-card-body {
        height: 100% !important;
    }
</style>



