import Vue from 'vue'
import router from './../router'

var config = new Vue({
    data: {
        loginStatus: 0,
        appInfo: {
            shop: {},
            user: {
                phone: null
            }
        },
        foodList: [],
        orderList: []
    },
    watch: {
        loginStatus: function (now, pre) {
            if (now === 1 && pre === 0) {
                if (this.appInfo.shop.shop_name === '') {
                    this.$Message.warning('Please set your shop info first')
                    router.push({
                        name: 'ShopEdit'
                    })
                } else {
                    router.push({
                        name: 'Home'
                    })
                }
            }
        }
    },
    methods: {
        getInfo: async function (cb = () => {}) {
            const result = await this.$axios.get('get_info')
            if (result.data.flag !== 8) {
                this.appInfo = result.data
                this.loginStatus = 1
                cb()
            } else {
                router.push({
                    name: 'Auth'
                })
            }
        }
    }
})
config.install = function () {
    Object.defineProperty(Vue.prototype, '$config', {
        get: function () {
            return config
        }
    })
}

export default config



