<template>
    <Layout class="home-box">
        <ButtonGroup :style="{marginBottom: '10px'}">
            <Button type="primary" @click="addCode">
                <Icon type="md-add" />
            </Button>
        </ButtonGroup>
        <Table
            :columns="codeColumns"
            :data="code"
            no-data-text="No data"
            :loading="codeLoading"
            >
        </Table>
    </Layout>
</template>
<script>
export default {
    name: 'Admin',
    data: function () {
        return {
            code: [],
            codeLoading: true,
            codeColumns: [
                {
                    title: 'Time',
                    key: 'time',
                    maxWidth: 200,
                    tooltip: true
                },
                {
                    title: 'Code',
                    key: 'code',
                    tooltip: true
                }
            ]
        }
    },
    created: function () {
        this.getCode()
    },
    methods: {
        getCode: async function () {
            const list = await this.$axios.get('/get_code')

            let codeArr = list.data
            codeArr.forEach((val, i) => {
                codeArr[i]['time'] = new Date(val.time * 1000).toLocaleString()
            })

            this.code = codeArr
            this.codeLoading = false
        },
        addCode: async function () {
            const result = await this.$axios.get('/add_code')

            if (result.status === 200 && result.data.flag === 'success') {
                this.getCode()
            }
        }
    }
}
</script>
<style scoped>
    .home-box {
        background-color: #fff;
    }
</style>



