<template>
    <Layout class="home-box">
        <Table
            :columns="orderColumns"
            :data="$config.orderList"
            no-data-text="No data"
            :loading="orderLoading"
            @on-row-click="clickOrder"
            >
        </Table>
        <Modal
            v-model="orderModal"
            :title="'Order details #' + (oIndex + 1)"
            footer-hide
            draggable
            @on-visible-change="modalToggle">
            <Table
                :columns="orderDetailColumns"
                :data="orderDetailData"
                no-data-text="No data"
                >
            </Table>
            <div :style="{marginTop: '10px'}">
                <Tag type="dot" color="primary">Order Status</Tag>
                <Select v-model="oStatus" style="width:200px" @on-change="ostatusChange">
                    <Option v-for="item in optionList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                </Select>
            </div>
        </Modal>
    </Layout>
</template>
<script>
export default {
    name: 'Orders',
    data: function () {
        return {
            orderModal: false,
            orderLoading: true,
            oStatus: undefined,
            oid: undefined,
            oIndex: undefined,
            orderColumns: [
                {
                    type: 'index',
                    width: 60,
                    align: 'center'
                },
                {
                    title: 'Time',
                    key: 'time',
                    maxWidth: 200,
                    tooltip: true
                },
                {
                    title: 'User',
                    key: 'user',
                    maxWidth: 200,
                    tooltip: true
                },
                {
                    title: 'Phone',
                    key: 'phone',
                    maxWidth: 200,
                    tooltip: true
                },
                {
                    title: 'Notice',
                    key: 'notice',
                    tooltip: true
                },
                {
                    title: 'Status',
                    key: 'status',
                    maxWidth: 100,
                    render: (h, params) => {
                        return h('div', [
                            h('strong', this.$lodash.filter(this.optionList, ['value', params.row.status])[0]['label'])
                        ])
                    }
                }
            ],
            orderDetailColumns: [
                {
                    title: 'Food name',
                    key: 'name',
                    tooltip: true
                },
                {
                    title: 'Count',
                    key: 'count',
                    width: 100,
                    tooltip: true
                },
                {
                    title: 'Price',
                    key: 'price',
                    width: 100,
                    tooltip: true
                }
            ],
            orderDetailData: [],
            optionList: [
                {
                    value: -1,
                    label: 'Cancelled'
                },
                {
                    value: 0,
                    label: 'Untreated'
                },
                {
                    value: 1,
                    label: 'Preparing'
                },
                {
                    value: 2,
                    label: 'Completed'
                },
                {
                    value: 3,
                    label: 'Finished'
                }
            ]
        }
    },
    computed: {
        status_loading: function () {
            return this.oStatus === undefined
        }
    },
    mounted: function () {
        this.getOrders()
    },
    methods: {
        getOrders: async function () {
            const list = await this.$axios.get('/orders', {
                params: {
                    start: 0
                }
            })
            const orderArr = list.data
            orderArr.forEach((val, i) => {
                orderArr[i]['time'] = new Date(val.time * 1000).toLocaleString()
            })
            this.$config.orderList = orderArr
            this.orderLoading = false
        },
        clickOrder: function (data, index) {
            let foodArr = []
            // console.log(data)
            this.oStatus = data.status
            this.oid = data._id
            data.order.forEach((val) => {
                foodArr.push({
                    name: val.order.name,
                    count: val.qtd,
                    price: val.order.price * val.qtd
                })
            })
            this.oIndex = index
            this.orderDetailData = foodArr
            this.orderModal = true
        },
        ostatusChange: async function (status) {
            const result = await this.$axios.get('/set_status', {
                params: {
                    status: status,
                    id: this.oid
                }
            })
            if (result.data.flag === 'success') {
                this.getOrders()
                this.$Message.success('Success')
            }
        },
        modalToggle: function (status) {
            if (!status) {
                this.oid = undefined
            }
        }
    }
}
</script>
<style scoped>
    .home-box {
        background-color: #fff;
    }
</style>



